.hr-agenda {
	display: grid;
	grid-template-columns: 100px 1fr 1fr 1fr 1fr 1fr;
	grid-template-rows: 50px repeat(15, 7%);
	background-color: white;
	@include shadow($shadow__level--1);
	&__day {
		text-transform: uppercase;
		font-weight: 500;
		font-size: 1rem;
		border-right: 1px solid #212121;
		border-bottom: 1px solid #aaa;
		text-align: center;
		padding: 0.5rem;
		&--last {
			border-right: none;
		}
	}
	&__time {
		display: flex;
		flex-wrap: wrap;
		color: black;
		text-transform: uppercase;
		font-size: 0.9rem;
		text-align: center;
		grid-column: 1 / span 1;
		border-top: 1px solid #aaa;
		border-right: 1px solid #aaa;
		padding: 0.8rem 0;
		line-height: 100%;
		justify-content: center;
	}
	&__timeslot {
		background: rgba(255,255,255,0.7);
		color: black;
		font-size: 0.65rem;
		padding: 0.5rem;
		border: 1px solid gray;
		font-weight: 300;
		border-radius: 2px;
	}
}

.hr-agenda__lecture--time {
	font-weight: 400;
}

.hr-agenda__classroom {
	margin: 0.4rem 0;
	font-weight: bold;
	visibility: hidden;
}

.monday { background: lightgray; grid-column: 2 / span 1; }
.tuesday { background: lightgray; grid-column: 3 / span 1; }
.wednesday { background: lightgray; grid-column: 4 / span 1; }
.thursday { background: lightgray; grid-column: 5 / span 1; }
.friday { background: lightgray; grid-column: 6 / span 1; }

