.monday {
	&.slc { @include slot(1, 2); }
	&.dev01 { @include slot(3, 4); }
	&.dev03 { @include slot(6, 7); }
	&.anl { @include slot(8, 9); }
}

.tuesday {
	&.slc { @include slot(1, 2); }
	&.dev01 { @include slot(3, 5); }
	&.anl { @include slot(8, 9); }
	&.anl01 { @include slot(11, 12); }
}

.wednesday {
	&.slc { @include slot(6, 7); }
	&.dev01 { @include slot(8, 10); }
	&.dev03 { @include slot(11, 12); }
}

.thursday.slc { @include slot(2, 3); }

.friday.dev01 { @include slot(4, 8);}