.overview {
    display: flex;
    width: 90%;
    height: 100%;
    &__list {
        display: flex;
        flex-grow: 7;
        flex-direction: column;
        background-color: white;
        margin: 16px;
        @include shadow($shadow__level--1);
        &--item {
            display: flex;
            width: 100%;
            height: 40px;
            align-items: center;
            border-bottom: 1px solid lightgray;
            padding: 0 16px;
            box-sizing: border-box;
        }
    }
    &__header {
        display: flex;
        width: 100%;
        height: 48px;
        align-items: center;
        border-bottom: 1px solid red;
        padding: 0 16px;
        box-sizing: border-box;
        &--item {
            display: flex;
            flex-grow: 1;
        }
    }
    &__filter {
        display: flex;
        flex: 5 0 200px;
        background-color: white;
        margin: 16px;
        @include shadow($shadow__level--1);

    }
}

.overview__list--name,
.overview__list--facility,
.overview__list--capacity,
.overview__list--outlets {
    display: flex;
    flex-grow: 1;
}
